.calendar {
    width: 1200px;
    max-width: 84vw;
    margin: 0 auto;
    background-color: var(--base-white);
    text-align: center;
}

.column {
    height: 1260px;
    width: 13%;
    border-radius: 12px 12px 0 0;
    background-color: var(--light-accent);
    transition: all 0.2s ease-in-out;
    position: relative;
}

.column p {
    font-size: 18px;
}

.column .subtitle {
    padding: 8px;
    height: 56px;
}

.column.utility {
    height: 0px;
    margin: 0 auto !important;
}

.utility .time-scale {
    top: 72px;
}

.event {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 8;
    margin-bottom: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border: 4px solid var(--dark-accent);
    background-color: var(--dark-accent);
    transition: 0.2s all ease-in;
}

.event:hover {
    cursor: pointer;
    color: black;
    background: repeating-linear-gradient(135deg,
            var(--light-accent),
            var(--light-accent) 8px,
            var(--dark-accent) 8px,
            var(--dark-accent) 12px);
    background-color: var(--dark-accent);
}

.selection {
    position: absolute;
    height: 25px;
    left: 0;
    right: 0;
    z-index: 4;
    cursor: pointer;
}

.selection:hover {
    background-color: var(--dark-accent);
}

.eventlist {
    width: 100%;
    margin: 24px 0;
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: space-between;
}

.time-scale {
    position: absolute;
    top: 60px;
}

.time-scale p {
    font-size: 18px;
    height: 50px;
}

.scale-left {
    left: -50px;
}

.scale-right {
    right: -50px;
}

.grid-lines {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    top: 60px;
    left: 0px;
    height: calc(100% - 60px);
    height: 1200px;
    width: 1200px;
    max-width: 84vw;
}

.grid-lines * {
    position: relative;
    width: 100%;
    height: 0.8px;
    z-index: 6;
    background-color: var(--base-dark-grey);
}

.switch {
    position: absolute;
    height: 60px;
    top: 12px;
    padding: 4px 20px;
    border-radius: 12px;
    transition: all 0.2s ease-in-out;
    font-weight: bold;
    border: none;
    background-color: var(--light-accent);
}

.switch:hover {
    cursor: pointer;
    background-color: var(--dark-accent);
}

.move-left {
    left: -70px;
}

.move-right {
    right: -70px;
}

.settings-row {
    flex-wrap: wrap;
}


@media only screen and (max-width: 1000px) {

    .eventlist {
        flex-direction: column;
        margin: 12px 0;
    }

    .column,
    .grid-lines {
        width: 70%;
        margin: 12px 15%;
    }

    .mono-hide {
        display: none;
    }

    .mono-show {
        display: inline-block
    }

    .scale-left {
        left: -65px;
    }
}

@media only screen and (max-width: 500px) {

    .column,
    .grid-lines {
        width: 60%;
        margin: 12px 20%;
    }

    .calendar {
        max-width: 94%;
    }

    .switch {
        padding: 0 12px;
    }

    .move-left {
        left: -50px;
    }

    .move-right {
        right: -50px;
    }

    .scale-left {
        left: -50px;
    }
}

@media only screen and (max-width: 369px) {

    .time-scale p {
        font-size: 16px;
    }

    .move-left {
        left: -45px;
    }

    .move-right {
        right: -45px;
    }
}